.blockTitle {
  position: absolute;

  height: auto;
  width: auto;
  padding: 0px 170px 22px 20px;
  margin-bottom: 130px;
  border-radius: 6px;
  background: black;

  @media screen and (max-width: 992px) {
    padding: 0px 100px 22px 20px;
    margin-bottom: 130px;
  }
  @media screen and (max-width: 768px) {
    padding: 0px 0px 5px 20px;
    margin-bottom: 0px;
    top: 350px;
    width: 250px;
  }
  @media screen and (max-width: 425px) {
    top: 300px;
  }
  // @media screen and (max-width: 620px) {
  //   padding: 2px 0px 5px 5px;
  //   margin-bottom: 0px;
  //   top: 350px;
  //   width: 170px;
  // }

  &::before,
  &::after {
    content: "";
    position: absolute;
    height: calc(100% + 5px);
    width: calc(100% + 5px);
    top: -3px;
    left: -3px;
    // border-radius: 18px;
    background: linear-gradient(
      45deg,
      #e6fb04,
      #ff6600,
      #00ff66,
      #00ffff,
      #ff00ff,
      #ff0099,
      #6e0dd0,
      #ff3300,
      #099fff
    );
    filter: blur(5px);
    z-index: -3;
    background-size: 200%;
    animation: homeAnim 420s linear infinite;

    @keyframes homeAnim {
      0% {
        background-position: 0 0;
      }
      50% {
        // background-position: 600% 0;
        background-position-x: 3000%;
      }

      100% {
        background-position: 0 0;
      }
    }
  }

  &::after {
    margin-top: 200px;
    width: calc(150%);
    left: -25%;
    top: 150px;
    height: 30px;
    transform: perspective(2rem) rotateX(10deg);
    filter: blur(50px);
    @media screen and (max-width: 992px) {
      margin-top: 0px;
    }
    @media screen and (max-width: 768px) {
      display: none;
    }
  }
}

