.home-main {
  padding-top: 50px;
  display: grid;
  align-items: center;
  justify-items: center;
  height: 100%;

  @media screen and (max-width: 420px) {
    height: 0px;
  }

  .main-content {
    user-select: none;
    h2 {
      font-size: 80px;
      position: relative;
      font-family: $font-2;
      z-index: 1;
      @media screen and (max-width: 992px) {
        font-size: 60px;
      }
      @media screen and (max-width: 768px) {
        font-size: 20px;
      }
      // @media screen and (max-width: 620px) {
      //   font-size: 12px;
      // }

      &:before {
        content: "";
        position: absolute;
        height: 180px;
        width: 180px;
        border-radius: 50%;
        border: 2px rgb(2, 19, 252) solid;
        top: -30px;
        left: -80px;
        z-index: -2;
        animation: h1Anim 1.5s forwards ease;
        transform: scale(0);

        @media screen and (max-width: 992px) {
          height: 122px;
          width: 122px;
          top: -17px;
          left: -47px;
        }
        @media screen and (max-width: 768px) {
          height: 50px;
          width: 50px;
          top: -12px;
          left: -27px;
        }
        // @media screen and (max-width: 620px) {
        //   height: 30px;
        //   width: 30px;
        //   top: -6.6px;
        //   left: -15px;
        //   border: 1px rgb(2, 19, 252) solid;
        // }

        @keyframes h1Anim {
          to {
            transform: scale(1);
          }
        }
      }
    }
    h3 {
      font-size: 80px;
      font-weight: 100;
      z-index: 3;
      @media screen and (max-width: 992px) {
        font-size: 60px;
      }
      @media screen and (max-width: 768px) {
        font-size: 20px;
      }
      @media screen and (max-width: 620px) {
        font-size: 14px;
      }
    }
  }
}

