.logo {
  margin-top: 20px;
  position: absolute;
  font-family: $font-2;
  font-size: 24px;
  user-select: none;
  // @media screen and (max-width: 620px) {
  //   font-size: 1rem;
  // }

  &:after {
    content: "";
    height: 50px;
    width: 50px;
    border: 1px $color-2 solid;
    border-radius: 50%;
    position: absolute;
    top: -8px;
    left: -20px;
    z-index: -1;
  }
}
